<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow"
      aria-label="Предыдущая страница"
      v-bind:class="{'pagination__link--disabled': currentPage == 1}"
      v-on:click.prevent="paginatePrev(currentPage)" href="#">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>

    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link"
      v-bind:class="{'pagination__link--current': pageNumber == page}"
      v-on:click.prevent="paginate(pageNumber)" href="#">
        {{ pageNumber }}
      </a>
    </li>

    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow"
      href="#" aria-label="Следующая страница"
      v-bind:class="{'pagination__link--disabled': currentPage == pages}"
      v-on:click.prevent="paginateNext(currentPage)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1,
    };
  },
  model: {
    props: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.currentPage = page;
      this.$emit('paginate', page);
    },
    paginatePrev(page) {
      if (this.currentPage !== 1) {
        this.currentPage -= 1;
        this.$emit('paginate', page - 1);
      }
    },
    paginateNext(page) {
      if (this.currentPage !== this.pages) {
        this.currentPage += 1;
        this.$emit('paginate', page + 1);
      }
    },
  },
};

</script>
